@import url(https://fonts.googleapis.com/css?family=Lato:100,100i,300,300i,400,400i,700,700i,900,900i&display=swap);
.back_button {
    position: absolute;
    display: block;
}

.container.fluid{padding:0 !important;
}
body {overflow: hidden!important;
}
@media only screen and (min-width:767x) and (max-width:991px){
body {overflow: hidden!important;
}
}


body{
  overflow-x:hidden;
  position:relative;
}

/* back button */
.back_button {
    display: none;
}
.downloadapk_img{
    height: 42px;
    margin-left: 12px;
}
body .dialog__content.dialog__content__active {
background: transparent !important;
}

/* .dialog.notification_pop {
width: 300px;
left: -30px;
bottom: -30px;
} */
.overlay:before {
    background-color: transparent !important;
}
.new_class_app{
  height: 35px;
  width: 85%;
  margin-left: 14px;
}

/* for cookies poilicy */
.overlay.overlay--active {
  z-index: 9!important;
}

/* for sticky header */
.navigationclass {
    top: 0 !important;
    position: fixed;
    width: 100% ;
}
















































































































































































